.mobilenav-wrapper {
  position: absolute;
  top: 63px;
  width: 100%;
  clip: rect(0, 9999px, 9999px, -9999px);
}

.mobilenav {
  background-color: #1a212e;
}

.mobilenav.on {
  transition: transform ease-in-out 0.4s, opacity ease-out 0.3s;
  transform: translateY(0);
}

.mobilenav.off {
  transition: transform ease-in-out 0.4s, opacity ease-out 0.3s;
  transform: translateY(-100%);
}

.mobilenavlinks {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.mobilenavlink {
  text-align: left;
  padding-top: 10px;
  padding-bottom: 10px;
}

.mobilenavlinks a {
  text-align: left;
  font-size: larger;
}

.navbar {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #23252a;
  border-bottom: 3px solid #e70b5c;
  pointer-events: fill;
}

.navbar a {
  cursor: pointer;
  font-weight: 600;
}

.navlink-wrapper {
  width: 20%;
}

.navlinks {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}

#hamburger-icon {
  width: 35px;
  height: 21px;
  position: relative;
  margin: 0px auto;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#hamburger-icon span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

#hamburger-icon span:nth-child(1) {
  top: 0px;
}

#hamburger-icon span:nth-child(2),
#hamburger-icon span:nth-child(3) {
  top: 9px;
}

#hamburger-icon span:nth-child(4) {
  top: 18px;
}

#hamburger-icon.open span:nth-child(1) {
  top: 9px;
  width: 0%;
  left: 50%;
}

#hamburger-icon.open span:nth-child(2) {
  transform: rotate(45deg);
}

#hamburger-icon.open span:nth-child(3) {
  transform: rotate(-45deg);
}

#hamburger-icon.open span:nth-child(4) {
  top: 9px;
  width: 0%;
  left: 50%;
}

